body{
    overflow-x: hidden;
}


::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}
::-webkit-scrollbar-track {
    background: #f1f1f100;
}
::-webkit-scrollbar-thumb {
    background: #2777FE;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
    background: #012b75;
}
